@font-face {
  font-family: "Roboto";
  src: url("../fonts/robotomedium.woff2") format("woff2"),
    url("../fonts/robotomedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto Condensed';
  src: url("../fonts/robotocondensedbold.woff2") format('woff2'),
      url("../fonts/robotocondensedbold.woff") format('woff');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto Condensed';
  src: url("../fonts/robotocondensedregular.woff2") format('woff2'),
      url("../fonts/robotocondensedregular.woff") format('woff');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'PT Mono';
  src: url("../fonts/ptmonoregular.woff2") format('woff2'),
      url("../fonts/ptmonoregular.woff") format('woff');
  font-weight: 400;
  font-style: normal;
}
